  .feedback__inputs-wrapper,
.feedback__submit-wrapper {
    grid-template-columns: 1fr;
  }
}

.feedback__inputs-wrapper {
  border: none;
  padding: 0;
  row-gap: 20px;
  column-gap: 29px;
  margin: 0 0 35px 0;
}
@media (max-width: 767px) {
  .feedback__inputs-wrapper {
    margin-bottom: 30px;
  }
}

.feedback__inputs-wrapper textarea,
.feedback__inputs-wrapper input {
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
}
.feedback__inputs-wrapper textarea::placeholder,
.feedback__inputs-wrapper input::placeholder {
  color: #ffffff;
}

.feedback__inputs-wrapper textarea {
  min-height: 155px;
  padding: 25px 15px;
}
@media (max-width: 767px) {
  .feedback__inputs-wrapper textarea {
    grid-column: 180px;
  }
}

.feedback__submit-wrapper {
  row-gap: 20px;
  column-gap: 50px;
  align-items: flex-end;
}

.feedback__inputs-wrapper textarea {
  grid-column: 1/3;
}
@media (max-width: 767px) {
  .feedback__inputs-wrapper textarea {
    grid-column: 1/2;
  }
}

.feedback__btn {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  line-height: 19px;
  order: -1;
  background: #041427;
}

.feedback__checkbox {
  border: none;
  padding: 0;
  margin: 0;
}

.feedback__checkbox label {
  padding: 8px 8px 0;
  display: block;
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  line-height: 16px;
  position: relative;
  padding-left: 27px;
  min-height: 24px;
}
.feedback__checkbox label::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 2px;
  left: 0;
  top: 30%;
}

.feedback__checkbox input:checked + label::before {
  background: url("../img/svg/arrow-checkbox.svg") no-repeat 20% center;
}

.footer {
  background: #041827;
  color: #ffffff;
}

.footer__container {
  padding: 36px 50px 55px;
  display: flex;
  justify-content: space-between;
  column-gap: 4%;
}
@media (max-width: 1023px) {
  .footer__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 30px;
    column-gap: 10%;
    padding: 60px 50px 75px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    display: flex;
    flex-direction: column;
    row-gap: 0;
    padding: 35px 0;
  }
}

.footer__link-logo {
  display: block;
  min-width: 215px;
  min-height: 55px;
  margin-bottom: 30px;
}

.footer__social-list {
  margin: 0 0 0 30px;
}
@media (max-width: 1023px) {
  .footer__social-list {
    margin: 0;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: center;
  }
}

.footer__inner {
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .footer__inner {
    display: flex;
    justify-content: space-between;
    grid-column: 1/3;
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    padding: 0 25px 25px;
    align-self: center;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .footer__nav {
    display: flex;
    flex-direction: column;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    padding: 0 25px;
  }
}

.social {
  list-style-type: none;
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  gap: 20px;
  padding: 0;
}

.social__icon {
  fill: #f8fdff;
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 30px;
  min-height: 30px;
  cursor: pointer;
  transition: opacity 0.2s ease;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.social__link.focus-visible:focus {
  opacity: 0.7;
}

.address {
  max-width: 308px;
}
@media (max-width: 767px) {
  .address {
    max-width: 100%;
    padding: 0 25px;
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}

.address h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  margin: 0 0 20px 0;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .address h3 {
    margin: 0;
    padding: 20px 0;
  }
}

.address__text {
  margin: 0;
  font-style: normal;
  color: rgba(255, 255, 255, 0.7);
}

.address__list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-style: normal;
}

.address__link {
  line-height: 25px;
  color: rgba(255, 255, 255, 0.7);
}

@media (max-width: 767px) {
  .address__inner {
    display: flex;
    gap: 10px;
    flex-direction: column;
    padding-bottom: 20px;
  }
}

.accordion {
  position: relative;
}
@media (max-width: 767px) {
  .accordion {
    cursor: pointer;
  }
}

.accordion__btn {
  display: none;
}
@media (max-width: 767px) {
  .accordion__btn {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 61px;
    border: none;
    background: transparent;
  }
  .accordion__btn::before, .accordion__btn::after {
    content: "";
    position: absolute;
    right: 40px;
    width: 15px;
    height: 1px;
    background-color: #c4c4c4;
  }
  .accordion__btn::before {
    transform: rotate(90deg);
  }
  .accordion__btn.no-js {
    display: none;
  }
}

@media (max-width: 767px) {
  .accordion__btn.open::before {
    transform: rotate(180deg);
  }
}

.open .accordion__touch-js {
  display: block;
}

@media (max-width: 767px) {
  .accordion__touch-js {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
  }
}

@media (max-width: 767px) {
  .accordion__touch-js.no-js {
    max-height: 100%;
    overflow: hidden;
  }
}

